<template lang="pug">
    #panel-wrapper     
        #panel-content
            h1.shake-little.shake-constant#loadingStatus(v-if="!panel_content_loaded") {{ status }} {{ kao }}
        .v-place-holder-2em
        button.btn.btn-primary(v-on:click.prevent="closePanel") Back
</template>

<script>
export default {
  name: "panel-1",
  data() {
    return {
      kao: "(\u30CE\u0414`)",
      status: "Thinking Hard"
    };
  },
  props: ["panel_content_loaded", "shouldClosed"],
  methods: {
    closePanel() {
      this.$emit("closePanel", {});
    },
    setStatus(status) {
      this.getElementById("panel-content").innerHTML = status;
    }
  },
  watch: {
    shouldClosed: {
      deep: true,
      handler (newValue, oldValue) {
        if (newValue.v) this.closePanel();
      }
    }
  }
};
</script>

<style scoped>
.btn {
  /*background-color: blueviolet;*/
  border: 1px solid blueviolet;
  border-bottom-left-radius: 0.25em;
  border-top-left-radius: 0.25em;
  border-bottom-right-radius: 0.25em;
  border-top-right-radius: 0.25em;
  width: 6em;
  height: 3em;
  /*box-shadow: 1px 2px 2px rgb(168, 135, 151);*/
  color: blueviolet;
  cursor: pointer;

  display: block;
  margin: auto;
  background-color: transparent;

  font-size: 1em;
}

.btn:hover {
  background-color: blueviolet;
  color: white;
}

#panel-wrapper {
  margin: 5%;
}

#loadingStatus {
  text-anchor: middle;
  text-align: center;
  display: block;
  margin: auto;
  height: 80vh;
  font-family: sans-serif;
  overflow-x: hidden;
  white-space: nowrap;
}

#loadingStatus::before {
  content: "";
  display: inline-block;
  height: 99%;
  vertical-align: middle;
}
</style>
