  mounted: function() {
    this.content_cache = new Map();
    let vm = this;
    window.onload = () => {
      this.activeHash = "#Index";
      window.location.hash = this.activeHash;
      // setInterval(this.hash_check, 100);
    };
    window.addEventListener('hashchange', function(e) {
      // if (window.location.hash === "#Index")
      // console.log(this.activeHash + "->" + window.location.hash);
      vm.activeHash = window.location.hash;
    }, false);
  },
  watch: {
    activeHash: function (newValue, oldValue)
    {
      if (newValue === "#Index" && oldValue != "#Index")
        this.shouldClosed.v = true;
        //history.deleteAll();
    }
  },
  methods: {
    // hash_check ()
    // {
    //   if (window.location.hash != this.activeHash) {
    //     // if (hash === "#Index")
    //    // console.log(this.activeHash + "->" + window.location.hash);
    //     this.activeHash = window.location.hash;
    //   }
    // },
    showPanel() {
      this.shouldClosed.v = false;
      if (this.panelInstance == null)
        this.panelInstance = this.$showPanel({
          component: "panel-1",
          cssClass: "panel-1",
          props: {
            shouldClosed: this.shouldClosed
          }
        });
      else this.panelInstance.show();
      //this.panelInstance.promise.then(result => { window.location.hash = "#Index"; })
    },
    get_content(event, info) {
      if (info.is_portal) {
        window.open(info.content_url, "_blank");
        return;
      }

      this.project_detail_loaded = false;
      this.showPanel();
      window.location.hash ="#" + info.title;

      if (info.content_url == undefined || info.content_url == null) {
        document.arrive(
          "#loadingStatus",
          { onceOnly: true },
          () =>
            (document.getElementById("loadingStatus").innerHTML =
              "(；´Д`)\nTarget URL unset")
        );
        return;
      }

      if (this.content_cache.has(info.content_url)) {
        console.log("Using cache for " + info.content_url);
        document.arrive(
          "#loadingStatus",
          { onceOnly: true },
          () =>
            (document.getElementById(
              "panel-content"
            ).innerHTML = this.content_cache.get(info.content_url))
        );
        return;
      } else {
        console.log("Getting " + info.content_url);
        this.$http
          .get(info.content_url, {
            // use before callback
            before(request) {
              // abort previous request, if exists
              if (this.previousRequest) {
                this.previousRequest.abort();
                console.log("Aborting ongoing request.");
              }

              // set previous request on Vue instance
              this.previousRequest = request;
            }
          })
          .then(
            response => {
              this.content_cache.set(info.content_url, response.body);
              document.getElementById("panel-content").innerHTML =
                response.body;
              this.project_detail_loaded = true;
              this.previousRequest = null;
            },
            response => {
              document.getElementById("loadingStatus").innerHTML =
                "(；´Д`)\nFailed";
              this.project_detail_loaded = true;
              this.previousRequest = null;
            }
          );
      }
    }
  }
};
