<template lang="pug">
    .info-card-wrapper(v-on:click.prevent="handler")
      .info-card
          .info-image-wrapper
              img.info-image(v-bind:src="info.image")
          .info-desc
              h3 {{ info.title }}
              p {{ info.brief }}
</template>

<script>
export default {
  name: "info-card",
  props: {
    info: {
      type: Object,
      required: true,
      default: null
    }
  },
  methods: {   
    handler() { 
      this.$emit('card-clicked', this.info);
    }
  }
};
</script>

<style scoped>
.info-card {
  align-items: center;
  height: 360px;
  display: flex;
  flex-direction: column;
  border-radius: 8px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  box-shadow: 0px 0px 0px rgba(196, 100, 159, 0.384);
  transform: scale(1.0, 1.0);
  transition: box-shadow 0.8s linear, transform 0.2s linear;
}

.info-card:hover {
  cursor: pointer;
  box-shadow: 0px 8px 20px rgba(196, 100, 159, 0.384);
  transform: scale(1.05, 1.05);
  transition: box-shadow 0.4s linear, transform 0.1s ease-out;
}

.info-desc{  
  background-color: whitesmoke;
  width: 100%;
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
  flex: 0 0 30%;
  word-spacing: 0.3em;
}

.info-desc>*{
  text-align: center;
}

.info-card>.info-image-wrapper {
  position: relative;
  width: 100%;
  flex: 0 0 70%;
  max-height: 70%;
  content: ' ';
  background-color: blueviolet;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
}

.info-card>.info-image-wrapper>.info-image {
  width: 100%;
  height: 100%;
  border-top-left-radius: inherit;
  border-top-right-radius: inherit;
  border-bottom-left-radius: inherit;
  border-bottom-right-radius: inherit;
  object-fit: cover;
}

.info-card>.info-image-wrapper:after {
  border-top-left-radius: inherit;
  border-top-right-radius: inherit;
  border-bottom-left-radius: inherit;
  border-bottom-right-radius: inherit;
  content: ' ';
  position: absolute;
  display: inline-block;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background: radial-gradient(circle, transparent 60%, rgb(177, 41, 218) 120%);
  mix-blend-mode: soft-light;
  opacity: 1;
  transition: opacity 1s linear;
}

.info-card>.info-image-wrapper:hover:after {
  opacity: 0;
  transition: opacity 0.5s linear;
}
</style>
